.neurosim-publication-block-container {
  text-align: center;
  padding: 20px;
}

.neurosim-publication-block-card {
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  transition: 0.3s;
  -webkit-transition: 0.3s;
  border-radius: 6px;
  width: 100%;
  height: 500px;
  text-align: left;
}
.neurosim-publication-block-card a {
  text-decoration: none;
}
.neurosim-publication-block-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-radius: 6px 6px 0 0;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: filter;
  transition-property: filter;
}
.neurosim-publication-block-card:focus img, .neurosim-publication-block-card:hover img {
  filter: brightness(115.69%);
}
.neurosim-publication-block-card:active img {
  filter: brightness(127.45%);
}

.neurosim-publication-block-text {
  padding: 14px 20px;
  margin-bottom: 8px;
}
.neurosim-publication-block-text h3.neurosim-publication-title {
  font-size: 24px;
  font-weight: 500;
  color: black !important;
  line-height: 24px;
  margin: 10px 0 !important;
}
.neurosim-publication-block-text h5.neurosim-publication-info {
  font-size: 18px;
  font-weight: 300;
  font-style: italic;
  color: black !important;
  line-height: 18px;
  margin: 10px 0 !important;
}
.neurosim-publication-block-text h6.neurosim-publication-authors {
  font-size: 15px;
  font-weight: 300;
  text-transform: none !important;
  color: black !important;
  line-height: 18px;
  margin: 10px 0 !important;
}
.neurosim-publication-block-text p.neurosim-publication-cite {
  font-size: 16px;
  font-weight: 400;
  color: black !important;
  line-height: 12px;
  margin: 10px 0 !important;
}